@import "https://fonts.googleapis.com/css?family=Roboto:100,100italic,300,300italic,regular,italic,500,500italic,700,700italic,900,900italic";
body {
  background: url("background.a1083ced.jpg");
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 19px;
}

.main {
  max-width: 420px;
  height: 533px;
  background-color: #bfd9e1;
  flex-direction: column;
  margin: 20px auto;
  padding: 8px;
  display: flex;
  position: relative;
}

.nav-panel {
  justify-content: space-between;
  margin-bottom: 8px;
  display: flex;
}

.nav-panel__settings, .nav-panel__signout {
  height: 32px;
  border: 1px solid #000;
  padding: 0 8px;
}

.window-wrap {
  height: 437px;
  vertical-align: bottom;
  background: url("background.a1083ced.jpg");
  border: 1px solid #000;
  margin-bottom: 8px;
  overflow-y: auto;
}

.window {
  min-height: 100%;
  flex-direction: column;
  justify-content: flex-end;
  display: flex;
}

.message {
  min-height: 52px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  max-width: 280px;
  word-break: break-word;
  background-color: #e5e5e5;
  margin: 0 8px 8px;
  padding: 8px;
}

.message_mine {
  text-align: right;
  margin-left: auto;
}

.message_mine_undelivered {
  background-color: #f7f7f7;
}

.message__time {
  text-align: right;
  font-size: 14px;
  font-style: italic;
  font-weight: 300;
  line-height: 16px;
}

.type__form, .settings__name__form {
  min-height: 32px;
  max-height: 64px;
  display: flex;
}

.type__form > *, .settings__name__form > * {
  border: 1px solid #000;
}

.type__input, .form__input {
  min-height: 32px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border: 1px solid #000;
  flex-grow: 5;
  margin-bottom: 8px;
  margin-right: 8px;
  padding-left: 7px;
}

.type__send, .form__submit {
  height: 32px;
  flex-grow: 1;
}

.tab-wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.tab {
  width: 273px;
  height: 319px;
  background-color: #fff;
  padding: 16px 14px;
  position: absolute;
  top: 106px;
  left: 24px;
  box-shadow: 0 0 63px 2px #444;
}

.tab__head {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.tab__close {
  width: 21px;
  height: 21px;
  background: url("close.91cc8c71.svg") no-repeat;
}

.settings__name__header {
  margin-bottom: 8px;
}

.settings__name__form__input {
  width: 207px;
  height: 32px;
}

.settings__name__form__submit {
  width: 32px;
  height: 32px;
}

.closed {
  display: none;
}

